import { Component, OnInit, ViewEncapsulation, ElementRef } from '@angular/core';
import { trigger, transition, style, animate, state } from '@angular/animations';

import * as $ from 'jquery';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class HomeComponent implements OnInit {
  /* Count weeks to show projects */
  countWeek: number = 1;
  show: boolean = true;

  /* Variables typewrite */
  dataType: any = ["I'm UI/UX developer.", "I love design apps.", "I can develop web/mobile apps."];
  loopNum:any = 0;
  toRotate:any;
  isDeleting:any = false;
  txt:any = '';
  el:any;
  period:any;

  constructor(private _elem: ElementRef) {
  }

  ngOnInit() {
    // console.log("Jquery: ", document);

    /* Typewrite */
    let elements = $('.typewrite');

    /* Get elements to type */
    for (let i = 0; i < elements.length; i++) {
      let el = elements[i];

      /* The elements */
      let toRotate = el.getAttribute('data-type');
      let period = el.getAttribute('data-period');

      if(toRotate) {
        // this.TxtType(el, JSON.parse(toRotate), period);
        this.el = el;
        this.toRotate = JSON.parse(toRotate);
        this.period = parseInt(period, 10) || 2000;
        this.TxtType();
      }
    }

    let css = document.createElement('style');
    css.type = 'text/css';
    css.innerHTML = ".typewrite > .wrap { border-right: 0.08em solid #fff}";
    document.body.appendChild(css);
  }

  /* Function typewrite */
  TxtType() {
    // console.log("my el:", this.toRotate);

    var i = this.loopNum % this.toRotate.length;
    // console.log("i", i);
    // console.log("rotate", this.toRotate[0]);
    let fullTxt = this.toRotate[i];

    if (this.isDeleting) {
      this.txt = fullTxt.substring(0, this.txt.length - 1);
    } else {
      this.txt = fullTxt.substring(0, this.txt.length + 1);
    }

    this.el.innerHTML = '<span class="wrap">' + this.txt + '</span>';

    var that = this;
    var delta = 200 - Math.random() * 100;

    if (this.isDeleting) { delta /= 2; }

    if (!this.isDeleting && this.txt === fullTxt) {
      delta = this.period;
      this.isDeleting = true;
    } else if (this.isDeleting && this.txt === '') {
      this.isDeleting = false;
      this.loopNum++;
      delta = 500;
    }

    setTimeout(() => {
      //console.log("el: ", this.el);
      this.TxtType();
    }, delta);
  }

  /* Function
  * add 1 to week
  */
  sumWeek(event:any):void {
    // console.log("ELEMT: ", this.renderer.setElementAttribute);
    /* avoid to refresh the page */
    event.preventDefault();
    // this.renderer.setElementClass('.deg1', '.deg1-back', true);
    if(this.show) {
      this.show = !this.show;
      setTimeout(() => {
        this.countWeek++;
        this.show = !this.show;
        console.log("Message");
      }, 2000);
    }
  }

}



